import React, { useCallback } from "react";
import { loadFull } from "tsparticles";
import Particles from "react-tsparticles";
import particlesConfig from "../../../config/particles-config";

const ParticleBackground = () => {
  const particlesInit = useCallback(async (engine: any) => {
    console.log(engine);
    await loadFull(engine); // Load the full tsparticles library
  }, []);

  const particlesLoaded = useCallback(async (container: any) => {
    console.log(container); // You can access the container object here
  }, []);

  return (
    <div id="particle-background">
      <Particles
        id="tsparticles"
        init={particlesInit}
        loaded={particlesLoaded}
        options={particlesConfig} // Make sure your particle configuration is correctly imported
      />
    </div>
  );
};

export default ParticleBackground;
